.nav {
  background-color: var(--navbar);
  backdrop-filter: blur(20px);
  position: fixed;
  width: 100%;
  left: 0;
  height: 75px;
  z-index: 1;
  top: 0;
}

.navContainer {
  font-family: "Poppins", sans-serif !important;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 900;
  height: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  margin: auto;
}

.container {
  min-height: 200vh;
  max-width: 750px;
  margin: auto;
}

.banner {
  margin-top: 75px;
  background: linear-gradient(45deg, #e99470 0%, #c9205d 100%);
  height: 150px;
  border-radius: 0 0 8px 8px;
}

.centerAvatar {
  display: flex;
  justify-content: left;
}

.avatar {
  transform: translateY(100px);
  width: 150px;
  height: 150px;
  margin-left: 14px;
  border-radius: 100%;
  overflow: hidden;
  border: 8px solid var(--background);
}

.avatar img {
  height: 100%;
  width: 100%;
}

.rowContent {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-left: 164px;
  margin-right: 14px;
  gap: 14px;
}

.btnWatch {
  height: fit-content;
  width: fit-content;
  background-color: var(--btnColor);
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 12px;
  font-weight: 600;
  font-size: 18px;
  color: var(--btnText);
}

